body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#header{
  height: 20mm;
  margin-top: -2.5mm;
  /*padding-top: 1em;*/
  padding-bottom: 1em;
}
#bfna{
  background-color: black;
  /*mask: url("./bfna.jpg") luminance center contain no-repeat;
  -webkit-mask: url("./bfna.jpg") luminance center contain no-repeat;*/
  -webkit-mask-image: url("./bfna.png");
  -webkit-mask-size: contain;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: center;
  height: 100%;
}
#bfwa{
  background-color: coral;
  /*mask: url("./bfna.jpg") luminance center contain no-repeat;
  -webkit-mask: url("./bfna.jpg") luminance center contain no-repeat;*/
  -webkit-mask-image: url("./bfwa.png");
  -webkit-mask-size: contain;
  -webkit-mask-repeat: no-repeat;
  -webkit-mask-position: center;
  height: 100%;
  position: relative;
  top: -100%;
}
    .component{
      position: absolute;
    }
    .component img{
      width: 100%;
    }
    #panel.hp1 #front{
      width: 5mm;
    }
    #panel.hp2 #front{
      width: 10mm;
    }
    #panel.hp3 #front{
      width: 15mm;
    }
    #panel.hp4 #front{
      width: 20mm;
    }
    #panel {
      height: 128.5mm;
      display: flex;
      justify-content: center;
    }
    #front{
      background-color: darkgrey;
      height: 100%;
      float:left;
      position:relative;
    }
    #shadow{
      float:left;
      background-color: black;
      width:1mm;
      height: 100%;
    }
    .hp1 .screw,
    .hp2 .screw{
      width:2.2mm;
      left: 0.9mm;
    }
    .screw{
      position: absolute;
      border-radius: 1.6mm;
      height:3.2mm;
      background-color: beige;
      box-shadow: inset -1mm 0 0 0 black;
      width:5.25mm;
      left: 4.875mm;
    }
    #topscrew{
      top:1.4mm;
    }
    #bottomscrew{
      bottom:1.4mm;
    }
    body{
      background-color:beige;
    }
